import idx from 'idx';

export default async function search(query) {
  const lunr = idx(await idx(window, _ => _.__LUNR__.__loaded), _ => _.en);
  const {index, store} = lunr || {};
  if (!query || !index || !store) return [];
  const results = index.search(query);
  return results.map(({ref}) => ({ref, ...store[ref]}));
}
